import React, { Component } from "react";
import PacMan from "../loader/pacMan";
import axios from "axios";
import { config } from "../../mainConfig";
import {
  Bar,
  BarChart,
  CartesianGrid,
  Legend,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts";

class Runtime extends Component {
  constructor(props) {
    super(props);
    this.platform = this.props.platform;
    this.type = this.props.type;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getRuntime = this.getRuntime.bind(this);
  }

  componentDidMount() {
    this.getRuntime();

    this.updateMetric = setInterval(this.getRuntime, config.updateInterval);
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getRuntime() {
    axios
      .get(`${config.apiUrl}/api/logs/import/runtime`, {
        params: {
          platform: this.platform,
          type: this.type,
        },
      })
      .then((response) => {
        this.setState({ data: response.data, loading: false });
      })
      .then(() => {
        console.log(this.state.data);
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false });
      });
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <ResponsiveContainer
          width={this.props.width}
          height={this.props.height}
        >
          <BarChart
            width={this.props.width}
            height={this.props.height}
            data={this.state.data}
            margin={{
              top: 20,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Bar dataKey="runtime" stackId="a" fill="#8884d8" />
            <Bar dataKey="sleep_time" stackId="a" fill="#82ca9d" />
          </BarChart>
        </ResponsiveContainer>
      );
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default Runtime;
